.Navbar{
    background-color: white;
    display: grid;
    width: 82%;
    grid-template-columns: 4fr 3fr;
    margin: 1.5vmax auto;
    /* padding: 0px 132px; */
}

.leftPart{
    justify-content: left;
    display: flex;
    align-items: center;
}
.leftPart>.speraxlogo img{
    width: 24px;
}
.textlinks {
    display: flex;
    align-items: center;
}
.linktextspa{
    margin: 0.6vmax;
    margin-left:0 ;
    text-decoration: none;
    padding-right: 1.5vmax;
    padding-left: 0.7vmax;
    font-family: Messina Modern;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
    letter-spacing: -0.03em;
    text-align: left;
    color: #1B2022;;
}

.linktext{
    margin: 0.7vmax;
    padding-right: 1.5vmax;
    color: #1B2022;;
    text-decoration: none;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: -0.03em;
    text-align: left;
    font-family:'MessinaSans';}
.linktext:hover{
    color: #37d4d1;
}
.leftPart:last-child img{
    width: 50%;
}
.Navbar>.rightPart{
    flex-direction: row;
    display:flex;
    text-align: right;
    justify-content:flex-end;
}
.Navbar>.rightPart>.social{
    padding-top: 1vmax;
    margin-right: 1vmax;
}
.Navbar>.rightPart>.social>a{
    padding: 1vmax;
}
.icon:hover{
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    transition: all 0.02s;
}
.Navbar>.rightPart>.app{
    border-radius: 90px;
    background-color: #31C1BF;
    width: 163.2px;
    height: 34px;
    border:0px;
}
.Navbar>.rightPart>.app:hover{
    background: #3AB1AB;
}
.Navbar>.rightPart>.app{
    color:white;
    text-decoration: none;
    text-align: center;
    align-items: center;
    padding-top: 0.55vmax;
    border: 1px solid #31C1BF;
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: -0.03em;

}

.mobileRight {
    display: none !important;
}
@media screen and (max-width:480px) {
    .linktext, .social{
        display: none;
    }
    .leftPart>.speraxlogo img {
        width: 28px;
    }
