        width: 28px;
        height: 28px;
    }
    .mobileMenuContainer {
        display: flex;
        flex-direction: column;
        background: white;
        text-align: center;
        z-index: 100;
        position: absolute;
        width: 100%;
        height: 200px;
        left: 0;
