        display: flex;
        align-items: center;
        margin-right: 16px;
    }
    .social {
        display: none !important;
    }
    .mobileRight {
        display: flex !important;
    }
    .deskTopRight {
        display: none !important;
    }
    .Navbar>.rightPart>.app {
        width: 125px;
        height: 40px;
        font-size: 14px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0;
    }
    .mobileMenu {
        display: block;
        flex-grow: 1;
        text-align: right;
        margin-left: 10px;
    }
    .leftPart{
        margin-left: 16px;
    }
    .mobileMenu>img {
        width: 24px;
        height: 24px;
    }
    
}
@media screen and (max-width: 1000px) and (min-width: 481px) {

    .Navbar{
        width: 91%;
